export default {
	data() {
		return {
			region:'选择位置',
			nodes:``,//富文本内容
			contactArr:[
				// {
				// 	name:'赵某某',phone:'15154546565',
				// },{
				// 	name:'钱某某',phone:'15154546565',
				// },{
				// 	name:'孙某某',phone:'15154546565',
				// },
			],
			form: {
				field03: '被盗',
				field04: '红',
				field05: '本地居家隔离',
				field06: '安全'
			},
			list1: [
				{
					value: '被盗',
					name: '被盗',
					checked: true
				},
				{
					value: '人身伤害',
					name: '人身伤害'
				},
				{
					value: '大量聚集',
					name: '大量聚集'
				}
			],
			list2: [
				{
					value: '红',
					name: '红',
					class:'one'
				},
				{
					value: '橙',
					name: '橙',
					class:'two'
				},
				{
					value: '黄',
					name: '黄',
					class:'three'
				},
				{
					value: '蓝',
					name: '蓝',
					class:'four'
				}
			],
			list3: [
				{
					value: '本地居家隔离',
					name: '本地居家隔离'
				},
				{
					value: '返程后居家隔离',
					name: '返程后居家隔离'
				},
				{
					value: '封路暂时不能返程',
					name: '封路暂时不能返程'
				},
				{
					value: '其余原因不能返程',
					name: '其余原因不能返程'
				}
			],
			list4: [
				{
					value: '安全',
					name: '安全'
				},
				{
					value: '低风险',
					name: '低风险'
				},
				{
					value: '高风险',
					name: '高风险'
				},
				{
					value: '未知',
					name: '未知'
				}
			]
		};
	},
	onLoad(e) {},
	methods: {
		handleCancelChoose(){
			this.$refs.linkage.hide()
		},
		showChoose(){
			this.$refs.linkage.show()
		},
		handleChoose({checkArr,checkValue,defaultVal,result}){
			this.$refs.linkage.hide()
			this.region = result
			this.form.field02 = result
		},
		handleRadioChange(evt, name) {
			this.form[name] = evt.target.value;
		},
		send() {
			let params = {
				...this.form,
				field01:uni.getStorageSync('userId')
			}
			this.$HTTP({
				url: '/safeData/save',
				params,
				successCallback: ({data}) => {
					if(data.code == 0){
						uni.showToast({
							title:'提交成功!'
						})
						setTimeout(()=>{
							uni.navigateBack()
						},1500)
					}else{
						uni.showToast({
							title:data.msg,
							icon:'none'
						})
					}
				}
			});
		}
	}
};
